import React from "react";
import "./Sidebar.css";
import SideNavContent from "./SideNavContent";
import { Transition } from "react-transition-group";
import SubContainer from "./SubContainer";
import { useAmazonContext } from "../../Contexts/AmazonContext";
import { Link } from "react-router-dom";

export default function Sidenav(props) {
  let {
    subContainer,
    courseTitleUserIsOn,
    setCourseTitleUserIsOn,
    courseDetails,
    setCourseDetails,
    topicDatafromBackEnd,
    setTopicDatafromBackEnd,
    exampleDatafromBackEnd,
    setExampleDatafromBackEnd,
    entryStore,
    setEntryStore,
    isDarkMode,
    setIsDarkMode,
  } = useAmazonContext();

  return (
    <div
      className="sidenav"
      style={
        props.state === "entering"
          ? { animation: "moveSideBar .3s forwards" }
          : props.state === "entered"
          ? { transform: "translateX(-0px)" }
          : { animation: "moveSideBar .3s reverse backwards" }
      }
    >
      <div className="sidenavHeader" style={{ background: "#dd6260" }}>
        {courseDetails.courseTitle && (
          <img
            className="courseSideNavImage"
            src={
              "https://qaleelo-assets.s3.us-east-2.amazonaws.com/" +
              courseDetails.courseTitle.replace(/\s/g, "") +
              "/courseImage/" +
              courseDetails.courseTitle.replace(/\s/g, "") +
              ".png"
            }
            alt=""
          ></img>
        )}
        <div className="newCloseBtn" onClick={props.click}>
          <i class="fa-solid fa-xmark"></i>
        </div>
      </div>

      <Transition in={!subContainer} timeout={300} unmountOnExit mountOnEnter>
        {(state) => <SideNavContent state={state} closeNav={props.click} />}
      </Transition>
      <Transition in={subContainer} timeout={300} unmountOnExit mountOnEnter>
        {(state) => <SubContainer state={state} closeNav={props.click} />}
      </Transition>
    </div>
  );
}
