import React, { useState } from "react";
// import Button from './components/Button.js'
// import Header from './components/Header.js'
// import Sidebar from './components/Sidebar.js'
// import "./components/sidebar/Sidebar.css";
import { useAmazonContext } from "./Contexts/AmazonContext";
import Footer from "./components/Footer";
import Topic from "./components/Topic";
import Quiz from "./components/Quiz";
import Button from "./components/Button";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import MainSideNav from "./components/sidebar/MainSideNav";
import CourseMenuPage from "./components/landingPage/CourseMenuPage";
import CourseLandingPage from "./components/landingPage/CourseLandingPage";
import CourseContent from "./components/landingPage/CourseContent";
import CourseStats from "./components/landingPage/CourseStats";
import Lesson from "./components/Lesson";

function App() {
  const { courseTitleUserIsOn, entryStore, isDarkMode, setIsDarkMode } =
    useAmazonContext();
  // Add a class to the body element based on the dark mode state
  document.body.className = isDarkMode ? "darkbody" : "lightbody";
  return (
    // <Lesson></Lesson>

    <Router>
      {/* <MainSideNav></MainSideNav> */}

      <Switch>
        <Route path="/" exact component={CourseMenuPage} />
        <Route path="/:courseTitle/topic/:topicNumber" component={Topic} />
        <Route path="/:courseTitle/quiz/:topicNumber" component={Quiz} />
        <Route path="/:courseTitle/start" component={CourseLandingPage} />
        <Route path="/:courseTitle/lesson/:lessonNumber" component={Lesson} />
      </Switch>
      {/* <Footer></Footer> */}
    </Router>
  );

  // return (
  //     <>
  //     <div>
  //     <Router>
  //     <Sidebar></Sidebar>
  //     </Router>

  //     </div>
  //     </>
  // )
}

export default App;
