import React, { useState, useEffect } from "react";
import { ColorExtractor } from "react-color-extractor";
import ColorThief from "colorthief";
import { useAmazonContext } from "../../Contexts/AmazonContext";

export default function SideNavRowQuiz(props) {
  const { setSubContainer, setSubContainerEntries, courseDetails } =
    useAmazonContext();
  const [dominantColor, setDominantColor] = useState("");
  const [textColor, setTextColor] = useState("");

  const openRow = () => {
    setSubContainer(true);
    setSubContainerEntries(props.entries);
  };

  return (
    <div
      className="sidenavRowLesson"
      style={{
        backgroundColor: "white",
        width: props.width,
        marginLeft: props.marginLeft,
        borderRadius: props.borderRadius,
        // backgroundColor: dominantColor,
      }}
    >
      <div className="circleQuiz">
        <span className="circle-numberLesson">{props.number}</span>
      </div>

      <img
        className="sideNavRowLessonImage"
        src="https://qaleelo-assets.s3.us-east-2.amazonaws.com/General/QuizImage.png"
        alt=""
        crossOrigin="anonymous"
      ></img>
      <div>
        <p
          style={{
            margin: 0,
            marginTop: "25px",
            color: "#a678de",
            fontSize: "2vh",
            fontWeight: "bold",
          }}
        >
          Chapter Quiz
        </p>
        <p className="sidenavRowLessonText" style={{ marginTop: 0 }}>
          {props.title}
        </p>
      </div>
    </div>
  );
}
